import React from 'react';
import StripeCheckout from 'react-stripe-checkout';

import './stripe-button.styles.scss';

const StripeCheckoutButton = ({ price }) => {
    const priceForStripe = price * 1000;
    const publishableKey = 'pk_test_51IEsEqBu1WuJxY54A0dmbNGxKF9YSC89QJmo6fUEld9pckEfT2gpdHvrwPOIgw2ITfxiUz0GReJRViTHFHNj0efk00JMwtxmjT';

    const onToken = token => {
        console.log(token);
        alert('Payment Successful');
    }

    return (
        <StripeCheckout 
            label='Pay Now'
            name='Selling Clothing Ltd.'
            billingAddress
            shippingAddress
            image='https://svgshare.com/i/TcL.svg'
            descriptiopn={`Your total is $${ price }`}
            amount={ priceForStripe }
            pannelLabel='Pay Now'
            token={ onToken }
            stripeKey={ publishableKey }
        />
    )
}

export default StripeCheckoutButton;