import React from 'react';
import { Redirect, Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux'; //Rx
import { createStructuredSelector } from 'reselect';

import './App.css';

import HomePage from './pages/homepage/homepage.component';
import ShopPage from './pages/shop/shop.component';
import SignInAndSignUpPage from './pages/sign-in-and-sign-up/sign-in-and-sign-up-out.component';
import Header from './components/header/header.component';
import { auth, createUserProfileDocument } from './firebase/firebase.utils';
import { setCurrentUser } from './redux/user/user.actions'; //Rx
import { selectCurrentUser } from './redux/user/user.selectors';
import CheckoutPage from './pages/checkout/checkout.component';
import CollectionPage from './pages/collection/collection.component';
import ContactPage from './pages/contact/contact-component';

//Let's convert function into class component
class App extends React.Component {

  /*constructor() {
    super();

    this.state = {
      currentUser: null
    }
  }*/

  unsubscribeFromAuth = null;

  componentDidMount() {
    const { setCurrentUser } = this.props; //Rx (this.props)

    this.unsubscribeFromAuth = auth.onAuthStateChanged(async userAuth => {
      
      if(userAuth)
      {
        const userRef = await createUserProfileDocument(userAuth);

        userRef.onSnapshot(snapShot => {
          /*this.setState({
            currentUser: {
              id: snapShot.id,
              ...snapShot.data()
            }
          })*/

          setCurrentUser({
            id: snapShot.id,
            ...snapShot.data()
          });
        });
      }

      /*this.setState({
        currentUser: userAuth
      });*/
      setCurrentUser(userAuth);
    });
  }

  componentWillUnmount() {
    this.unsubscribeFromAuth();
  }

  render() {
    return (
      <div>
        <Header /*currentUser={this.state.currentUser}*/ />      
        <Switch>
  
          <Route path="crwn-clothing/" exact>
              <Redirect to="crwn-clothing" />
          </Route>
  
          <Route exact path="/crwn-clothing" component={ HomePage } />
          <Route exact path="/crwn-clothing/shop" component={ ShopPage } />
          <Route exact path='/crwn-clothing/checkout' component={ CheckoutPage } />
          <Route exact path='/crwn-clothing/shop/:collectionId' component={ CollectionPage } />
          <Route exact path='/crwn-clothing/contact' component={ ContactPage } />

          <Route 
            exact 
            path='/crwn-clothing/signin' 
            render={() => this.props.currentUser ? (
                <Redirect to='/crwn-clothing' />
              ) : (
                <SignInAndSignUpPage />
              ) 
            } 
          />
        </Switch>     
      </div>
    );
  }
}

/*2*/
// const mapStateToProps = ({ user }) => ({
//   currentUser: user.currentUser
// });

const mapStateToProps = createStructuredSelector({
  currentUser: selectCurrentUser
});

/*1*/
const mapDispatchToPtops = dispatch => ({
  setCurrentUser: user => dispatch(setCurrentUser(user))
});

/*3*/
// export default App;
export default connect(mapStateToProps, mapDispatchToPtops)(App);
