import React from 'react';

import './contact.styles.scss';

const ContactPage = () => (
    <div className='contact-form'>
        <h1>CONTACT FORM - (not working yet)</h1>
        <label for="fname">First Name</label>
        <input className='contact-input' type="text" id="fname" name="firstname" placeholder="Your name.." />

        <label for="lname">Last Name</label>
        <input className='contact-input' type="text" id="lname" name="lastname" placeholder="Your last name.." />

        <label for="country">Country</label>
        <select className='contact-input' name="country">
            <option value="australia">Australia</option>
            <option value="canada">Canada</option>
            <option value="usa">USA</option>
        </select>

        <label for="subject">Subject</label>
        <textarea className='contact-input contact-textarea' name="subject" placeholder="Write something.." />

        <input class='contact-submit' type="submit" value="Submit" />
    </div>
)

export default ContactPage;