import React from 'react';

import './checkout.styles.scss';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

import CheckoutItem from '../../components/checkout-item/checkout-item.component';
import { selectCartItems, selectCartTotal } from '../../redux/cart/cart.selectors';

import StripeCheckoutButton from '../../components/stripe-button/stripe-button.component';

const CheckoutPage = ({ cartItems, total }) => (
    <div className='checkout-page'>
        <div className='checkout-header'>
            <div className='header-block'>
                <span>Product</span>
            </div>
            <div className='header-block'>
                <span>Description</span>
            </div>
            <div className='header-block'>
                <span>Quantity</span>
            </div>
            <div className='header-block'>
                <span>Price</span>
            </div>
            <div className='header-block'>
                <span>Remove</span>
            </div>
        </div>
        {
            cartItems.map(cartItem => (
                <CheckoutItem key={ cartItem.id } cartItem = { cartItem } />
            ))
        }

        <div className='total'>
            <span>TOTAL: ${total}</span>
            <StripeCheckoutButton price={ total } />
        </div>
        <div className='test-warning'>
            *Please use the following thes credit card for Payment
            <br/>
            <span className='bold'>VISA card number: </span> 
            <span className='red'>4242 4242 4242 4242</span> -
            <span className='bold'> Exp.date (any future date) for example: </span>
            <span className='red'>05/21</span> - 
            <span className='bold'> CVC: </span>
            <span className='red'>123</span>
        </div>
    </div>
)

const mapStateToProps = createStructuredSelector({
    cartItems: selectCartItems,
    total: selectCartTotal
});

export default connect(mapStateToProps)(CheckoutPage);